import React, { useState, useCallback, useEffect } from "react";
import { Nav, Navbar, Container, Form, Button } from "react-bootstrap";
import { Routes, Route, Outlet, Link } from "react-router-dom";

export default function AppSidebar(props) {

    const menus = props.menus || []
    const active = props.active || '';
    const [searchText, setSearchText] = useState('');

    
    const onMenuItemClick = (e) => {        
        e.preventDefault();
        e.stopPropagation();
        return props.onMenuItemClick(e);
    };

    const onSearchTextChange = (e) => {
        e.preventDefault();
        e.stopPropagation();

        setSearchText( e.target.value.toLowerCase() );
        return false;
    }

    const onSearchTextSubmit = (e) => {
        e.preventDefault();
        e.stopPropagation();
        return false;
    }

    //console.log('active', active);

    return (
        <div className="sidebar-sticky mt-2">
            <Form>
                <Form.Group className="mx-2" controlId="sidesearch">
                    <Form.Control type="text" placeholder="search" onChange={onSearchTextChange} onSubmit={onSearchTextSubmit} />
                </Form.Group>
            </Form>

            <div style={{                           
                    overflow: "hidden scroll",
                    height: "85vh",
                }}>

                <Nav className="flex-column" variant="pills" 
                    defaultActiveKey={active}>                   

                    {menus
                        .filter( item => 
                            (searchText === '') || (item.text.toLowerCase().indexOf(searchText) >= 0) )

                        .map( item => 
                            <Nav.Item>
                                <Nav.Link href={item.href} 
                                    eventKey={item.id} 
                                    onClick={onMenuItemClick}                                
                                    className={ (item.href === active) ? 'nav-link-active' : '' }
                                    >{item.text}</Nav.Link>
                            </Nav.Item>
                        )
                    }

                </Nav>
            </div>
        </div>
    );
}