import React, { useState, useCallback, useEffect } from "react";
import { render } from "react-dom";
import { Container, Row, Navbar, Nav, NavItem, Col } from "react-bootstrap";
import 'bootstrap/dist/css/bootstrap.min.css';
import { Routes, Route, Outlet, Link } from "react-router-dom";
import { PacmanLoader } from "react-spinners";

import './App.css';

import AppNavbar from "./AppNavbar";
import GalleryMain from "./GalleryMain";

function objectMap(obj, func) {
    return Object.fromEntries(Object.entries(obj).map(([k, v]) => [k, func(v)]));
}


const PUBLIC_IMAGE_BASEURL = 'https://raw.githubusercontent.com/madame-natt/images/master/'

function App() {
    const [isPageLoaded, setPageLoaded] = useState(false);
    const [title, setTitle] = useState('');

    const [apps, setApps] = useState({});
    const [photos, setPhotos] = useState({});

    
    useEffect( () => {
        setTimeout(() => {
            setPageLoaded(false);
            reload().then( () => setPageLoaded(true) );
        }, 3000);
    }, []);

    const reload = async() => {

        let _apps = await fetch('apps.json').then( res => res.json() );

        let _photos = await fetch('photo.json').then( res => res.json() );

        _photos = _photos.map( item => {
            let app_name = _apps[item.app_id];
            item.id = item.app_id;
            item.src = PUBLIC_IMAGE_BASEURL + item.src;
            return {
                ...item,                
                app_name: app_name,
                title: app_name + ' ' + item.src 
            }
        });


        setApps(_apps);
        setPhotos(_photos);
        
        //reloadSidemenu();
        //reloadPhotos();
        console.log('reload');        
    };


    
    return (      
        <div data-bs-theme="dark">
            <AppNavbar title={title} />

        {(isPageLoaded) ? (

            <Routes>
                <Route index element={<GalleryMain apps={apps} photos={photos} />} />
                <Route path="*" element={<GalleryMain apps={apps} photos={photos} />} />
            </Routes>
            
        ) : (

            <Container fluid>
                <PacmanLoader cssOverride={{ display: "block", margin: "350px auto", borderColor: "red" }} />
            </Container>

        )}

      </div>
    );
}

export default App;
