import React, { useState, useCallback, useEffect } from "react";
import { render } from "react-dom";
import { Container, Row, Navbar, Nav, NavItem, Col } from "react-bootstrap";

// https://github.com/neptunian/react-photo-gallery
import Gallery from "react-photo-gallery";
import GalleryPhoto from './GalleryPhoto';
import Carousel, { Modal, ModalGateway } from "react-images";

import AppSidebar from "./AppSidebar";
import { useLocation } from "react-router-dom";


export default function GalleryMain(props) {
    const location = useLocation();

    const [currentImage, setCurrentImage] = useState(0);
    const [isViewerDialogOpen, setViewerDialogOpen] = useState(false);

    const [imagesGallery, setImagesGallery] = useState([]);
    const [imagesFilters, setImagesFilters] = useState({});
    const [sidebarMenus, setSidebarMenus] = useState([]);
    const [sidebarActive, setSidebarActive] = useState('');
    const [appTitleName, setAppTitleName] = useState('');

    const [photoCount, setPhotoCount] = useState(0);

    const apps = props.apps || {};
    const photos = props.photos || {};
    
    useEffect(() => {
        reloadSidemenu();
        //reloadPhotos();        
        
    }, [apps, photos]); 

    const reloadSidemenu = () => {
        let sidemenu = [];
        sidemenu.push({
            'href': '/?all=1',
            'text': '(ALL)'
        });        
        for(var i in apps) {
            sidemenu.push({
                'href': '/?app_id=' + i,
                'text': apps[i] || '~Unknown~',
                'id': i
            });
        }

        /*sidemenu = sidemenu.sort( (a, b) => {
            let str1 = a.text;
            let str2 = b.text;
            return ( ( str1 == str2 ) ? 0 : ( ( str1 > str2 ) ? 1 : -1 ) );
        });*/
        //console.log(sidemenu);        
        setSidebarMenus( sidemenu );


        let selected = Math.floor(Math.random()* sidemenu.length)

        selectMenuItem( sidemenu[selected].href, { 'app_id': sidemenu[selected].id} );
        
    }


    const reloadPhotos = (filters) => {

        let images = [];

        if (!filters)
            filters = {};

        if (!photos)
            return;

        console.log(filters);

        if (filters.app_id) {
            images = photos.filter( item => {            
                return item.app_id && (item.app_id === filters.app_id)
            });
        } else {
            console.log(photos);
        }

        images = images.sort( (a, b) => {
            return (new Date(a.modified)) - (new Date(b.modified));
        });

        //if (filters.reverse)
            images.reverse();

        setImagesFilters(filters);
        setImagesGallery(images);
        setPhotoCount(images.length);
    }


    const openLightbox = useCallback((event, { photo, index }) => {
        setCurrentImage(index);
        setViewerDialogOpen(true);
    }, []);

    const closeLightbox = () => {
        setCurrentImage(0);
        setViewerDialogOpen(false);
    };


    const selectMenuItem = (path, params) => {
        setSidebarActive(path);

        const app_id = params.app_id;// href.substr(href.indexOf('#') + 1);
        //console.log(params);
        //reloadPhotos({ 'app_id': app_id });
        reloadPhotos( params );

        setAppTitleName(apps[app_id]);
    }

    const handleSidebarMenuItemClick = (e) => {
        e.preventDefault();        
        e.stopPropagation();

        const href = e.target.href;
        const location = new URL(href);

        const addr = location.pathname + location.search + location.hash;
        const params = Object.fromEntries( new URLSearchParams(location.search) );
        selectMenuItem(addr, params);
        return false;
    }

    return (
        <Container fluid>
            <Row>
                <Col sm={2} id="sidebar-wrapper">
                    <div
                        style={{
                            position: "relative",
                            height: "95vh",
                        }}
                    >
                        <AppSidebar
                            style={{ position: "fixed" }}
                            menus={sidebarMenus}
                            active={sidebarActive}
                            onMenuItemClick={handleSidebarMenuItemClick}
                        />
                    </div>
                </Col>
                <Col sm={10} id="page-content-wrapper">
                    <h2 className="mt-2">{appTitleName} ({photoCount})</h2>
                    <Gallery
                        photos={imagesGallery}
                        renderImage={GalleryPhoto}       
                        onClick={openLightbox}
                    />
                    <ModalGateway>
                        {isViewerDialogOpen ? (
                            <Modal onClose={closeLightbox}>
                                <Carousel
                                    currentIndex={currentImage}
                                    views={imagesGallery.map((x) => ({
                                        ...x,
                                        srcset: x.srcSet,
                                        caption: x.title,
                                    }))}
                                />
                            </Modal>
                        ) : null}
                    </ModalGateway>
                </Col>
            </Row>
        </Container>
    );
}
